.background-nav {
  background: #122538;
  margin-top: 10px;
  width: 100vw;
  height: 104px;
}

.cont {
  display: flex;
  justify-content: space-between;
  justify-items: center;
  padding-top: 1rem;
}
.cont1 {
  display: flex;
  flex-direction: row;
}
.cont2 {
  display: flex;
}
.imgRozetka {
  width: 65px;
  height: 65px;
  margin-left: 35px;
  margin-right: 16px;
}
.imgRozetkaText {
  width: 200px;
  position: absolute;
  /* top: 0; */
  left: 100px;
  top: -10px;
}

.p1-cont2 {
  color: white;
  font-size: 18px;
  font-family: 'OpenSans-Regular';
}
.p2-cont2 {
  color: #7f9799;
  font-size: 20px;
  margin-left: 25px;
  font-family: 'OpenSans-Regular';
}
.p1-cont2 span {
  text-decoration: underline;
}
.p2-cont2 span {
  color: white;
  font-size: 20px;
}
.vectorObject1 {
  padding-top: 15px;

  margin-left: 3rem;
  margin-right: 0.5rem;
  height: 35px;
}

.vectorObject2 {
  padding-top: 15px;

  height: 35px;
  margin-right: 1rem;
  margin-left: 1rem;
}
.vectorObject3 {
  padding-top: 15px;

  height: 35px;
  margin-right: 1rem;
}
@media (max-width: 778px) {
  .imgRozetka {
    width: 30px;
    height: 29px;
    margin-left: 12px;
    margin-top: -8px;
  }
  .imgRozetkaText {
    top: 7px;
    width: 100px;
    left: 50px;
  }
  .cont2 {
    margin-top: -0.5rem;
  }
  .p1-cont2 {
    display: none;
  }
  .p2-cont2 {
    display: none;
  }
  .vectorObject1 {
    height: 16px;
    padding-top: 8px;
    padding-left: 7px;
    padding-right: 7px;
  }
  .vectorObject2 {
    height: 16px;
    padding-top: 8px;
    padding-left: 7px;
    padding-right: 7px;
  }
  .vectorObject3 {
    height: 16px;
    padding-top: 8px;
    padding-left: 7px;
    padding-right: 7px;
  }
  .background-nav {
    margin-top: 0px;
    height: 45px;
  }
  .cont1H1 {
    padding-top: 4px;
    font-size: 12px;
  }
}
