@font-face {
  font-family: 'OpenSans-Regular';
  src: url('./fonts/OpenSans-Regular.ttf');
}
@font-face {
  font-family: 'OpenSans-SemiBold';
  src: url('./fonts/OpenSans-SemiBold.ttf');
}

body {
  margin: 0;
  padding: 0;
}

.backgroundUp {
  background-image: url('./img/fone2.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  /* background: red; */
  /* background-position-y: 100px; */
  padding-top: 41px;
  width: 100%;
  height: 80vh;
  /* height: 100vh; */
}

.backgroundDown {
  background-image: url('./img/fonedown.png');
  background-size: 100%;
  width: 100%;
  height: 14vh;
}
@media only screen and (min-width: 300px) and (max-width: 415px) {
  .backgroundUp {
    height: 140vh;
  }
  .backgroundDown {
    height: 30vh;
  }
}
@media only screen and (min-width: 416px) and (max-width: 778px) {
  .backgroundUp {
    height: 140vh;
  }
}
@media only screen and (min-width: 779px) and (max-width: 1200px) {
  .backgroundUp {
    height: 160vh;
  }
}
a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}
