footer {
  display: flex;
  width: 100%;
  margin: none;
}
footer img {
  width: 1.3rem;
  margin-top: 0.2rem;
}
.image1,
.image2,
.image3,
.image4,
.image5,
.image6 {
  /* padding: 1rem; */
  background: white;
  height: 0.5rem;
  margin-left: 1rem;
  margin-top: 0.5rem;
  padding: 0.2rem;
  height: 2rem;
}

footer .one {
  background: #1f2a3a;
  width: 60%;
  margin-right: 0;
}
footer .one .first {
  margin-top: 1rem;
}

footer .one .cont1 p {
  color: white;
  margin-left: 46px;
  line-height: 12px;
  font-family: 'OpenSans-Regular';
  margin: 20px;
}

footer .one .cont1 .second {
  display: flex;
  flex-direction: row;
  /* margin-top: 90px; */
  margin-top: 2rem;
  margin-left: 55px;
}

footer .one .cont1 .second .image1 img {
  margin-left: 7px;
  margin-right: 7px;
  padding: 0;
}

footer .one .cont1 .second .image2 img {
  margin-left: 7px;
  margin-right: 7px;
  padding: 0;
}

footer .one .cont1 .second .image3 img {
  margin-left: 7px;
