  padding: 0;
}

footer .one .cont1 .second .image4 img {
  margin-left: 7px;
  margin-right: 7px;
  padding: 0;
}

footer .one .cont1 .second .image5 img {
  margin-left: 7px;
  margin-right: 7px;
  padding: 0;
}

footer .one .cont1 .second .image6 img {
  margin-left: 7px;
  margin-right: 7px;
  padding: 0;
}

footer .three p {
  color: #002f34;
  margin-top: 3rem;
  margin-left: 5rem;
}
footer .two {
  width: 0;
  height: 0;
  border-top: 56px solid #5a9e4e;
  border-left: 70px solid #1f2a3a;
  border-bottom: 56px solid #5a9e4e;
  margin-right: 0;
}

footer .three {
  background: #5a9e4e;
  width: 40%;
  font-family: 'OpenSans-Regular';
}

