.form {
  background: #122538;
  width: 40%;
  position: absolute;
  top: 600px;
  right: 0;
  margin-right: 2.2rem;
  margin-right: 7rem;
}

.form .container {
  padding-left: 30px;
  padding-right: 30px;
}

.form .container .h1CRMSystem {
  color: white;
  font-size: 51px;
  text-align: center;
  font-family: 'OpenSans-SemiBold';
  padding: none;
  margin: none;
}

.form .container h3 {
  color: white;
  font-size: 25px;

  padding-left: 2rem;
  font-family: 'OpenSans-SemiBold';
}
.flexingBox {
  position: relative;
}
.h1CRMSystem {
}
.form .greenLogo {
  width: 15rem;
  top: -115px;
  left: 13rem;
  position: absolute;
}
.form .container p {
  text-align: center;
  color: white;
  font-family: 'OpenSans-SemiBold';
  height: 16px;
}

form {
  display: flex;
  flex-direction: column;
}
form input {
  /* height: 0.7rem; */

  outline: none;
  border: none;

  margin-bottom: 7px;
  font-size: 17px;
  font-family: 'OpenSans-SemiRegular';
  /* width: 40%; */
}
.emailInputMask {
  padding: 0.7rem;
  border-radius: 0.3rem;
  margin-bottom: 0.5rem;
}
.inputInputMask {
  padding: 0.7rem;
  border-radius: 0.3rem;
  margin-bottom: 0.5rem;
}
.phoneInputMask {
  padding: 0.7rem;
  border-radius: 0.3rem;
  margin-bottom: 0.5rem;
}
.nameInputMask {
  padding: 0.7rem;
  border-radius: 0.3rem;
  margin-bottom: 0.5rem;
}
.flexingBoxForDomen {
  display: flex;
  justify-content: space-around;
  background: white;
  padding: 0.7rem;
  border-radius: 0.3rem;
  margin-bottom: 0.5rem;
  justify-items: auto;
}
#domen {
  width: 40%;
}
form button {
  background: #00a046;
  width: 100%;
  /* height: 43px; */
  border: none;
  font-size: 17px;
  color: white;
  border-radius: 0.1rem;
  margin-left: auto;
  margin-right: auto;
  margin-top: 19px;
  margin-bottom: 2rem;

  padding-top: 1rem;
  padding-bottom: 1rem;
}
.placeholderDomen {
  text-align: center;
  /* padding-right: 14rem; */
}

