.green {
  color: green;
}
.red {
  color: red;
}
.gray {
  color: gray;
}
@media (max-width: 778px) {
  form button {
    width: 155px;
    height: 32px;
    font-size: 12px;
    margin-top: 6px;
  }
  .form .container {
    padding-right: 26px;
    padding-left: 26px;
  }
  .form .container h3 {
    font-size: 20px;
  }

  .form .container h3 span {
    font-size: 17px;
  }

  .form .container p {
    font-size: 13px;
  }
}

