.someContainers {
  display: grid;
  width: 600px;
  margin-top: 2rem;
  margin-left: 2rem;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
}
.someContainers div:hover {
  /* box-shadow: -5px -5px #00a046; */
}
@media (min-width: 300px) and (max-width: 520px) {
  .someContainers {
    /* display: grid; */
    grid-template-columns: 165px 165px;
    margin-left: 0rem;
    width: auto;
    /* margin: auto; */
    margin-top: 2rem;
  }
}
@media (max-width: 1200px) {
  .someContainers {
    justify-content: center;
    margin: auto;
    margin-top: 2rem;
  }
}
